import React from 'react'
import { GatsbyImage } from 'gatsby-plugin-image'
import { useStaticQuery, graphql } from 'gatsby'

/**
 * Image
 *
 * @param {string} src ç”»åƒã®URL
 * @param {string} alt ç”»åƒã®èª¬æ˜Ž
 * @return {JSX.Element}
 */
const Image = ({ src, alt = '' }) => {
  const data = useStaticQuery(graphql`
    query {
      allFile(filter: { sourceInstanceName: { eq: "attachments" } }) {
        edges {
          node {
            id
            childImageSharp {
              gatsbyImageData(layout: FULL_WIDTH)
            }
          }
        }
      }
    }
  `)

  // ç”»åƒãƒªã‚¹ãƒˆã®ä¸­ã‹ã‚‰ã€ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆå¼•æ•°ã§æŒ‡å®šã—ãŸURLã®ç”»åƒã‚’æŠ½å‡º.
  const targetEdge = data.allFile.edges.find(edge => edge.node.id === src)

  // ç”»åƒãŒå–å¾—ã§ããŸå ´åˆã®ã¿gatsby-imageã®ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆã‚’è¿”å´.
  return (
    targetEdge?.node.childImageSharp && (
      <GatsbyImage
        image={targetEdge.node.childImageSharp.gatsbyImageData}
        alt={alt}
      />
    )
  )
}

export default Image
